.ScrollContainer {
	display: flex;
	overflow-x: scroll;
	padding-bottom: 12px;
}

.btnText .vkuiButton__content {
	font-size: 17px;
    font-size: var(--vkui--font_title3--font_size--regular);
    line-height: 22px;
    line-height: var(--vkui--font_title3--line_height--regular);
    font-weight: 600;
    font-weight: var(--vkui--font_title3--font_weight--regular);
	transform: translateY(-1px);
}

.mv4 {
    margin-left: 4px;
    margin-right: 4px;
}

.cropItem {
    text-align: center;
}

.CustomSelectAsVk .react-select__control {
    border-radius: 10px;
    border: 1px solid var(--field_border);
}

.CustomSelectAsVk .react-select__control--is-focused {
    border-color: var(--accent);
    border: 1px solid var(--accent);
}
.CustomSelectAsVk .react-select__menu {
    z-index: 10000;
}